.section.quote {
    display: block;
    .quote-text{
        font-size: 1.2em;
        line-height: 1.3;
        color: var(--text-color);
        svg {
            color: var(--primary-color);
            font-size: 1.5em;
            margin-right: .5em;
            margin-left: .5em;
        }
    }
    .quote-author {
        color: var(--text-light);
        text-align: right;
        margin-top: .5em;
    }
}