.previous-searches {
    display: block;
    h2{
        color: var(--text-color);
        margin-bottom: .5em;
    }
    .previous-searches-container {
        .search-item {
            background-color: var(--primary-color);
            display: inline-block;
            padding: .5em 1em;
            margin: 0 1em 1em 0;
            color: #fff;
            border-radius: 30px;
            box-shadow: 0 5px 8px var(--shadow-color);
            transform: translateX(100%);
            opacity: 0;
            animation: getAnimationDuration(.5s) slideInRight forwards;
        }
    }
}
.search-box {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 35px;
    input, .btn {
        height: 30px;
    }
    input {
        width: 250px;
        padding: 0 .5em;
        border-radius: 5px 0 0 5px;
        outline: none;
        border: 1px solid #4d4d4d;
        
    }
    .btn {
        padding-block: 0;
        padding-inline: .75em;
        border-radius: 0 5px 5px 0;
    }
}
